body, html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  background-color: #333;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  display: flex;
}

.search-form {
  width: 80%;
  max-width: 500px;
}

.search-form input {
  width: 70%;
  border: 2px solid #444;
  border-radius: 5px;
  margin-right: 10px;
  padding: 10px;
}

.search-form button {
  width: 20%;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(4, 1fr);
  margin: 20px;
  display: grid;
}

.photo-card {
  border: 1px solid #ddd;
  border-radius: 5px;
  flex-direction: column;
  display: flex;
  overflow: hidden;
}

.photo-card img {
  width: 100%;
  height: auto;
  object-fit: cover;
  flex-grow: 1;
}

.info {
  background-color: #f8f8f8;
  justify-content: space-around;
  padding: 10px;
  font-size: .9em;
  display: flex;
}

.info-item {
  text-align: center;
}

.info-item b {
  display: block;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 1rem;
  transition: background-color .3s;
  display: none;
}

.load-more:hover, .load-more:focus {
  background-color: #0056b3;
  outline: none;
}

.load-more.show {
  display: block;
}

@media (max-width: 1200px) {
  .gallery {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 900px) {
  .gallery {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 600px) {
  header {
    flex-direction: column;
  }

  .search-form {
    width: 80%;
    flex-direction: column;
    align-items: center;
    display: flex;
  }

  .search-form input, .search-form button {
    width: 100%;
    margin: 5px 0;
  }

  .search-form button {
    height: 40px;
  }

  .gallery {
    grid-template-columns: 1fr;
  }
}

